<template>
  <div class="sign-up-page">
    <div class="logo">
      <img src="" alt="">
    </div>
    <div class="container">
      <form action="">
        <cube-input
          v-model="email"
          placeholder="请输入您的邮箱地址"
          type="text"
          :autofocus="autofocus"
          :clearable="clearable"
          ref="email"
        ></cube-input>
        <div class="verify-code">
          <cube-input
            v-model="verifyCode"
            placeholder="请输入验证码"
            type="text"
            :autofocus="autofocus"
            :clearable="clearable"
          ></cube-input>
          <cube-button
            @click="getVerifyCode"
          >获取验证码</cube-button>
        </div>
        <cube-input
          v-model="pwd"
          placeholder="请设置您的登录密码"
          type="text"
          :autofocus="autofocus"
          :clearable="clearable"
          ref="pwd"
          class="pwd"
        ></cube-input>
        <cube-button
          class="sign-up-btn"
        >注 册</cube-button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      apiHost: '',
      autofocus: true,
      clearable: true,
      email: '',
      verifyCode: '',
      pwd: ''
    }
  },
  props: {
  },
  created() {
    this.apiHost = this.hostParamas.host;
  },
  methods: {
    getVerifyCode () {
      if (!this.email) {
        const toast = this.$createToast({
          time: 1000,
          txt: '请填写邮箱',
          type: 'warn'
        })
        toast.show()
        return false
      } else if (!/^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/.test(this.email)) {
        const toast = this.$createToast({
          time: 1000,
          txt: '邮箱格式有误',
          type: 'warn'
        })
        toast.show()
        return false
      }

      this.$http.get(this.apiHost+'/sendCode', {
        params: {
          email: this.email
        }
      })
      .then((res) => {
        console.log(res);
      })
    }
  }
}
</script>

<style lang="scss">
.sign-up-page{
  height:100vh;
  background-color: #efeff4;
  .logo{
    height:1.066667rem;
    text-align: center;
    padding-top:4.0rem;
    padding-bottom:0.8rem;
    img{
      height:100%;
    }
  }
  form{
    display: flex;
    flex-direction: column;
    .verify-code{
      display: flex;
      margin-top:0.266667rem;
      &>.cube-input{
        flex: 1;
      }
      button{
        max-width: 2.666667rem;
      }
    }
    .pwd{
      margin-top:0.266667rem;
    }
    .sign-up-btn{
      margin-top:0.8rem;
    }
  }
}
</style>
