<template>
  <div class="sign-in-page">
    <div class="logo">
      <img src="" alt="">
    </div>
    <div class="container">
      <form action="">
        <cube-input
          v-model="username"
          placeholder="请输入您的用户名"
          type="text"
          :clearable="clearable"
        ></cube-input>
        
        <cube-input
          v-model="password"
          placeholder="请输入您的密码"
          type="password"
          :clearable="clearable"
          :eye="eye"
          class="password"
        ></cube-input>
        <cube-button
          class="sign-in-btn"
          @click="signIn"
        >登 录</cube-button>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      username: '',
      password: '',
      clearable: true,
      eye: true
    }
  },
  props: {
  },
  methods: {
    signIn() {
      if (!this.username) {
        this.$createToast({
          txt: '请输入用户名',
          type: 'warn',
          time: 1000
        }).show()
        return false
      }

      if (!this.password) {
        this.$createToast({
          txt: '请输入密码',
          type: 'warn',
          time: 1000
        }).show()
        return false
      }

      
    }
  }
};
</script>
<style lang="scss" scoped>
.sign-in-page{
  height:100vh;
  background-color: #efeff4;
  .logo{
    height:1.066667rem;
    text-align: center;
    padding-top:4.0rem;
    padding-bottom:0.8rem;
    img{
      height:100%;
    }
  }
  form{
    display: flex;
    flex-direction: column;
    .password{
      margin-top:0.266667rem;
    }
    .sign-in-btn{
      margin-top:0.8rem;
    }
  }
}
</style>
